			"desc": "<p>You imbue a creature you touch with positive energy to undo a debilitating effect. You can reduce the targets exhaustion level by one, or end one of the following effects on the target:</p><p>- One effect that charmed or petrified the target</p><p>- One curse, including the targets attunement to a cursed magic item</p><p>- Any reduction to one of the targets ability scores</p><p>- One effect reducing the targets hit point maximum</p>",
