			"desc": "<p>You touch a willing creature who isnt wearing armor, and a protective magical force surrounds it until the spell ends. The targets base AC becomes 13 + its Dexterity modifier. The spell ends if the target dons armor or if you dismiss the spell as an action.</p>",
